* {
  box-sizing: border-box; }

html {
  font-size: 10px; }

body {
  font-family: "Montserrat", sans-serif;
  font-size: 16px; }

a {
  text-decoration: none;
  color: inherit; }

.wrapper {
  max-width: 1200px;
  margin: 0 auto; }

.flex-wrap {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap; }

.b-logo a {
  font-size: 30px;
  font-weight: bold; }

/*#Header start*/
.b-header {
  position: absolute;
  width: 100%;
  padding-top: 2rem;
  font-family: "Montserrat", sans-serif;
  color: #fff; }
  .b-header__nav {
    display: flex;
    justify-content: space-between;
    margin: 18px 55px 0 auto;
    width: 40.7%; }
    .b-header__nav a {
      font-size: 14px;
      text-transform: uppercase;
      padding-bottom: 7px; }
      .b-header__nav a:hover {
        color: #FCE38A;
        border-bottom: 3px solid #FCE38A; }
  .b-header__tools {
    display: flex;
    justify-content: space-between;
    width: 6%;
    margin-top: 18px;
    font-size: 16px; }
    .b-header__tools .icon {
      display: inline-block;
      padding: 0 5px; }
      .b-header__tools .icon:hover {
        color: #FCE38A; }

/*#Main section start*/
.b-main-section {
  background: linear-gradient(0deg, rgba(252, 227, 138, 0.9) 0%, rgba(243, 129, 129, 0.9) 100%), url("../img/header-bg.jpg") no-repeat;
  background-size: cover;
  background-position: center;
  padding: 150px 0 25px 0; }

.b-main {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #fff; }
  .b-main__top-title {
    font-family: "KaushanScript", sans-serif;
    font-size: 72px;
    margin-bottom: 25px; }
  .b-main__head-title {
    margin: 0;
    width: 67%;
    text-align: center;
    text-transform: uppercase;
    line-height: 2;
    font-family: "Montserrat", sans-serif;
    font-weight: bold;
    font-size: 150px;
    line-height: 0.933; }
    .b-main__head-title:after {
      content: "";
      display: block;
      background-color: white;
      width: 60px;
      height: 3px;
      margin: 60px auto 53px auto; }

.b-btn {
  border: 3px solid white;
  background-color: transparent;
  width: 158px;
  height: 38px;
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  font-weight: bold;
  text-transform: uppercase; }

/*#Slider-header*/
.b-main__slider-controlls {
  margin-top: 150px;
  font-family: "Montserrat", sans-serif;
  color: #fff;
  font-size: 24px;
  text-transform: uppercase;
  counter-reset: item; }
  .b-main__slider-controlls ul {
    padding-left: 0;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap; }
  .b-main__slider-controlls li {
    position: relative;
    display: inline-block;
    width: 280px;
    padding-top: 25px;
    list-style: none; }
    .b-main__slider-controlls li:before {
      content: "0" counter(item);
      counter-increment: item;
      font-weight: bold; }

.slider-link {
  padding-left: 10px; }
  .slider-link:before {
    content: "";
    width: 100%;
    height: 3px;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #fff; }

/*#About section*/
.b-section {
  padding: 90px 0 110px 0; }
  .b-section__top-title {
    margin: 0;
    font-family: "KaushanScript", sans-serif;
    font-size: 24px;
    font-weight: normal;
    text-align: center; }
  .b-section__main-title {
    margin-top: 10px;
    font-family: "Montserrat", sans-serif;
    font-size: 30px;
    text-align: center;
    text-transform: uppercase; }
    .b-section__main-title:after {
      content: "";
      display: block;
      width: 60px;
      height: 3px;
      margin: 33px auto;
      background-color: #f38181; }
  .b-section__description {
    margin: 0;
    padding: 10px 120px 100px 120px;
    font-family: "Roboto", sans-serif;
    font-size: 14px;
    text-align: center;
    color: #999999;
    line-height: 1.6; }

/*#Media gradiated item*/
.b-media {
  position: relative;
  cursor: pointer;
  transition: all .75s;
  color: #fff; }
  .b-media img {
    display: block;
    max-width: 100%; }
  .b-media:hover {
    transform: translate(-5%, -5%);
    box-shadow: 10px 10px 0 #95e1d3; }
    .b-media:hover .b-media__grad {
      opacity: 1; }
  .b-media__grad {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(0deg, rgba(252, 227, 138, 0.9) 0%, rgba(243, 129, 129, 0.9) 100%);
    opacity: 0;
    transition: all .75s; }
  .b-media__info {
    position: relative;
    font-family: "Montserrat", sans-serif;
    font-weight: bold;
    font-size: 18px;
    text-transform: uppercase;
    text-align: center; }
    .b-media__info:before {
      font-family: "social";
      content: "";
      position: absolute;
      top: -100%;
      left: 50%;
      font-size: 20px;
      transform: translate(-50%, 0); }

/* #Favts section */
.b-facts-section {
  padding: 80px 0;
  background-color: #95E1D3; }

.b-fact {
  color: #fff;
  width: 20%;
  text-align: center; }
  .b-fact__number {
    font-size: 72px;
    font-weight: bold; }
  .b-fact__name {
    font-size: 14px;
    text-transform: uppercase; }

.b-services {
  margin-top: -10px; }
  .b-services .icon {
    position: absolute;
    top: 45px;
    left: 45px;
    color: #95E1D3;
    font-size: 33px; }
  .b-services__item {
    position: relative;
    width: 33.33%;
    padding: 40px 0 50px 0; }
    .b-services__item:nth-child(-n + 3) {
      border-bottom: 1px solid #999999; }
    .b-services__item-title {
      margin: 0;
      padding-left: 90px;
      font-size: 14px;
      font-weight: normal;
      text-transform: uppercase; }
    .b-services__item-content {
      margin-bottom: 0;
      padding-left: 90px;
      font-family: "Roboto", sans-serif;
      font-size: 14px;
      color: #999999;
      line-height: 1.6; }
